//dimension
export const useDimension = (hValue, wValue) => {
  const dimention = document.documentElement.clientHeight
  const width = document.documentElement.clientWidth
  const height = dimention - hValue
  const emptyItem = dimention - hValue
  const isMobile = width <= 500 ? true : false
  return { dimention, height, emptyItem, width, isMobile }
}

export const useEntryDimention = (showTargetRepack) => {
  const dimention = document.documentElement.clientWidth
  let height = document.documentElement.clientHeight
  let heightAdj = height - (showTargetRepack ? 535 : 290)
  height = dimention <= 500 ? (showTargetRepack ? (heightAdj + 100) : heightAdj) : height

  return { height, heightAdj, dimention }
}

// Stock Take
export const useStockTake = ({ dispatch }) => {
  const moduleName = 'stockTake'
  const zeroQuantity = useSelector(state => state.zeroQuantity)
  const thereAreRequest = useSelector(state => state.thereAreRequest);
  const moduleFirstLoad = useSelector(state => state.stockTakeFirstLoad)
  const warehouseUuid = useSelector((state) => state.user.warehouseUuid);
  const status = useSelector((state) => state.status);
  const isLoading = useSelector((state) => state.isLoading);
  const stockTakeEntryData = useSelector((state) => state.stockTakeEntryData);

  const [summary, setSummary] = useState(null)
  const [localEntryData, setLocalEntryData] = useState()
  const [search, setSearch] = useState()
  const [area, setArea] = useState()
  const [areaId, setAreaId] = useState({ label: "Storage Ambient", value: 1 })
  const [discard, setDiscard] = useState()

  // use Effect
  useEffect(() => {
    async function getData() {
      const ripeness = await getRipenessLevel({ dispatch })
      const areaList = await getWarehouseArea({ dispatch, warehouseUuid })
      setArea(areaList)
      const c = await getUnfinishedEntries({ dispatch, moduleName, isExist: stockTakeEntryData ? true : false, ripeness })
    }
    getData()
  }, []);

  useEffect(() => {
    setLocalEntryData()
    setSummary()
  }, [areaId])

  return {
    moduleName,
    thereAreRequest,
    moduleFirstLoad,
    warehouseUuid,
    status,
    isLoading,
    stockTakeEntryData,
    summary,
    setSummary,
    localEntryData,
    setLocalEntryData,
    search,
    setSearch,
    area,
    setArea,
    areaId,
    setAreaId,
    discard,
    setDiscard,
    zeroQuantity
  }
}

// Stock Take Entry
export const useStockTakeEntry = ({ moduleEntry, activeSku }) => {
  const [isSuccess, setIsSuccess] = useState({ success: false, name: null })
  const [confirmation, setConfirmation] = useState({ show: false, name: null })
  const [discard, setDiscard] = useState(false)
  const [isSaved, setIsSaved] = useState()
  const [action, setAction] = useState()
  const [isRequired, setIsRequired] = useState(false)
  const [hideContent, setHideContent] = useState(false)
  const [allowPage, setAllowPage] = useState([
    '/inventory-entry/stock-take/weight-entry',
    '/inventory-entry/stock-take/weight-entry/remove-sku',
    '/inventory-entry/stock-take/weight-entry/add-sku',
  ])

  const isLoading = useSelector(state => state.isLoading)
  const status = useSelector(state => state.status)
  const { skuName, skuNumber, poList, gradeList, totalQty, unitOfMeassures } = moduleEntry.jobPost.list[activeSku]
  const totalWeight = totalWeightCount({ moduleEntry, activeSku })
  const edited = jobSheetChecker({ moduleEntry })

  const postInfo = isSuccess.success && isSuccess.status ? true : false

  useEffect(() => {
    if (isSuccess.status === 500) {
      setIsSaved(false)
      setConfirmation({ show: false, name: confirmation.name })
    }
    else if (isSuccess.status === 200) {
      setConfirmation({ show: false, name: confirmation.name })
      setIsSaved(true)
    }
  }, [isSuccess])

  useEffect(() => {
    if (!isSaved) setIsSuccess({ success: false, name: null })
  }, [isSaved])

  return {
    isSuccess,
    setIsSuccess,
    confirmation,
    setConfirmation,
    discard,
    setDiscard,
    isSaved,
    setIsSaved,
    action,
    setAction,
    isRequired,
    setIsRequired,
    hideContent,
    setHideContent,
    allowPage,
    setAllowPage,
    isLoading,
    skuName,
    skuNumber,
    poList,
    gradeList,
    totalQty,
    unitOfMeassures,
    totalWeight,
    edited,
    postInfo,
    status
  }
}

// Stock Movement
export const useStockMovement = ({ dispatch }) => {
  const moduleName = 'stockMovement'

  const thereAreRequest = useSelector(state => state.thereAreRequest);
  const moduleFirstLoad = useSelector(state => state.stockMovementFirstLoad)
  const warehouseUuid = useSelector((state) => state.user.warehouseUuid);
  const stockMovementDropdown = useSelector(state => state.stockMovementDropdown)
  const areaId = useSelector(state => state.areaId)
  const areaIdDesposition = useSelector(state => state.areaIdDesposition)
  const areaMovement = useSelector(state => state.areaMovement)
  const status = useSelector((state) => state.status);
  const isLoading = useSelector((state) => state.isLoading);
  const stockMovementEntryData = useSelector((state) => state.stockMovementEntryData);
  const wasteReasonList = useSelector(state => state.wasteReasonList)

  const [summary, setSummary] = useState(null)
  const [localEntryData, setLocalEntryData] = useState()
  const [isCheckedBefore, setIsCheckedBefore] = useState(0)
  const [search, setSearch] = useState('')
  const [area, setArea] = useState()
  const [areaMatched, setAreaMatched] = useState()
  const [discard, setDiscard] = useState()

  const isCheckedTotal = selectedSkuCheck({ moduleEntry: localEntryData, moduleName });
  const added = isCheckedBefore !== isCheckedTotal && isCheckedTotal > 0 ? true : false
  const entryIsReady = localEntryData && localEntryData?.jobPost?.list?.length > 0 ? true : false

  const stockMovementTargetRepack = useSelector(state => state.stockMovementTargetRepack)

  useEffect(() => {
    async function getData() {
      const ripeness = await getRipenessLevel({ dispatch })
      const areaList = await getWarehouseArea({ dispatch, warehouseUuid })
      setArea(areaList)
      const c = await getUnfinishedEntries({ dispatch, moduleName, isExist: stockMovementEntryData ? true : false, ripeness })
      if (!wasteReasonList?.length) {
        getWasteReason({ dispatch })
      }
      setIsCheckedBefore(isCheckedTotal)
    }
    getData()
  }, []);

  useEffect(_ => {
    if (areaId && areaIdDesposition && areaId !== areaIdDesposition) setAreaMatched(true)
    else setAreaMatched(false)
  }, [areaId, areaIdDesposition, added])

  useEffect(() => {
    setLocalEntryData()
    setSummary()
  }, [areaId, areaIdDesposition])

  useEffect(_ => {
    if (stockMovementTargetRepack) {
      const isMatchedDropdown = areaId && areaIdDesposition && areaId !== areaIdDesposition
      if (isMatchedDropdown) {
        setSummary()
        getStockTakeSummary({
          moduleName,
          stockMovementTargetRepack,
          dispatch,
          desc: search,
          warehouseUuid,
          areaId,
          areaIdDesposition,
          setModule: setSummary,
          module: localEntryData
        })
        setSearch(search)
      }
    }
    if (!stockMovementTargetRepack) setSummary()
  }, [stockMovementTargetRepack])


  return {
    moduleName,
    thereAreRequest,
    moduleFirstLoad,
    warehouseUuid,
    stockMovementDropdown,
    areaId,
    areaIdDesposition,
    areaMovement,
    status,
    isLoading,
    stockMovementEntryData,
    summary,
    setSummary,
    localEntryData,
    setLocalEntryData,
    isCheckedBefore,
    setIsCheckedBefore,
    search,
    setSearch,
    area, setArea,
    areaMatched, setAreaMatched,
    discard,
    setDiscard,
    isCheckedTotal,
    added,
    entryIsReady,
    stockMovementTargetRepack
  }
}

