// Repacking
export const useRepacking = ({ dispatch }) => {
  const moduleName = 'repack'

  const thereAreRequest = useSelector(state => state.thereAreRequest);
  const moduleFirstLoad = useSelector(state => state.repackFirstLoad)
  const warehouseUuid = useSelector((state) => state.user.warehouseUuid);
  const status = useSelector((state) => state.status);
  const isLoading = useSelector((state) => state.isLoading);
  const repackEntryData = useSelector((state) => state.repackEntryData);
  const warehouseArea = useSelector(state => state.warehouseArea);
  const targetRepack = useSelector(state => state.targetRepack)

  const [summary, setSummary] = useState(null)
  const [localEntryData, setLocalEntryData] = useState()
  const [isCheckedBefore, setIsCheckedBefore] = useState(0)
  const [search, setSearch] = useState('')
  const [area, setArea] = useState()
  const [areaId, setAreaId] = useState()
  const [areaDestinationId, setAreaDestinationId] = useState()
  const [discard, setDiscard] = useState()

  const isCheckedTotal = selectedSkuCheck({ moduleEntry: localEntryData, moduleName });
  const added = isCheckedBefore !== isCheckedTotal && isCheckedTotal > 0 ? true : false

  const setTargetRepack = (value) => {
    dispatch({ type: IS_LOADING, targetRepack: value })
  }

  useEffect(() => {
    async function getData() {
      const ripeness = await getRipenessLevel({ dispatch })
      const areaList = await getWarehouseArea({ dispatch, warehouseUuid })
      setArea(areaList)
      const aId = areaList?.filter(area => area.label === 'Production')
      const adId = areaList?.filter(area => area.label === 'Staging')
      if (aId?.length) setAreaId(aId[0])
      if (adId?.length) setAreaDestinationId(adId[0])
      const c = await getUnfinishedEntries({ dispatch, moduleName, isExist: repackEntryData ? true : false, ripeness })
      setIsCheckedBefore(isCheckedTotal)
    }
    getData()
  }, []);
  useEffect(() => {
    setLocalEntryData()
    setSummary()
  }, [areaId])

  useEffect(_ => {
    if (targetRepack) {
      setSummary()
      if (areaId) getStockTakeSummary({
        moduleName,
        targetRepack,
        dispatch,
        desc: '',
        warehouseUuid,
        areaId,
        areaIdDesposition: areaDestinationId,
        setModule: setSummary,
        module: localEntryData
      })
    }
    if (!targetRepack) setSummary()
  }, [targetRepack, areaId])

  return {
    moduleName,
    thereAreRequest,
    moduleFirstLoad,
    warehouseUuid,
    status,
    isLoading,
    repackEntryData,
    warehouseArea,
    summary,
    setSummary,
    localEntryData,
    setLocalEntryData,
    isCheckedBefore,
    setIsCheckedBefore,
    search,
    setSearch,
    area,
    setArea,
    areaId,
    setAreaId,
    areaDestinationId,
    setAreaDestinationId,
    discard,
    setDiscard,
    isCheckedTotal,
    added,
    targetRepack,
    setTargetRepack
  }
}

// Repacking Entry
export const useRepackingEntry = ({ moduleEntry, activeSku }) => {
  const isLoading = useSelector(state => state.isLoading)
  const status = useSelector(state => state.status)
  const [invalidThreshold, setInvalidThreshold] = useState()
  const [isSuccess, setIsSuccess] = useState({ success: false, name: null })
  const [confirmation, setConfirmation] = useState({ show: false, name: null })
  const [discard, setDiscard] = useState(false)
  const [isSaved, setIsSaved] = useState()
  const [action, setAction] = useState()
  const [isRequired, setIsRequired] = useState(false)
  const [hideContent, setHideContent] = useState(false)
  const [allowPage, setAllowPage] = useState([
    '/inventory-entry/repacking/weight-entry',
    '/inventory-entry/repacking/weight-entry/remove-sku',
    '/inventory-entry/repacking/weight-entry/add-sku',
  ])

  const { skuName, skuNumber, poList, gradeList, totalQty, unitOfMeassures } = moduleEntry.jobPost.list[activeSku]
  const totalWeight = totalWeightCount({ moduleEntry, activeSku })
  const totalPack = totalPackCount({ moduleEntry, activeSku })
  const edited = jobSheetChecker({ moduleEntry })
  const postInfo = isSuccess.success && isSuccess.status ? true : false

  useEffect(() => {
    if (isSuccess.status === 500) {
      setIsSaved(false)
      setConfirmation({ show: false, name: confirmation.name })
    }
    else if (isSuccess.status === 200) {
      setConfirmation({ show: false, name: confirmation.name })
      setIsSaved(true)
    }

  }, [isSuccess])
  useEffect(() => {
    if (!isSaved) setIsSuccess({ success: false, name: null })
  }, [isSaved])

  return {
    isLoading,
    invalidThreshold,
    setInvalidThreshold,
    isSuccess,
    setIsSuccess,
    confirmation,
    setConfirmation,
    discard,
    setDiscard,
    isSaved,
    setIsSaved,
    action,
    setAction,
    isRequired,
    setIsRequired,
    hideContent,
    setHideContent,
    allowPage,
    setAllowPage,
    unitOfMeassures,
    totalWeight,
    edited,
    postInfo,
    skuName,
    skuNumber,
    poList,
    gradeList,
    totalQty,
    totalPack,
    status
  }
}

// Single view
export const useSingleView = ({ category, dispatch, setLocalEntryData, setSummary, setSearch, getStockTakeSummary, search, warehouseUuid, areaId, localEntryData, moduleName, stockMovementTargetRepack }) => {

  //submit
  const [action, setAction] = useState()
  const [confirmation, setConfirmation] = useState({ show: false, name: 'submit' })
  const [isSuccess, setIsSuccess] = useState({ success: false, name: null })
  const [isSaved, setIsSaved] = useState()

  // create po single view
  const [createPo, setCreatePo] = useState()
  const [singleViewData, setSingleViewData] = useState()
  const [fromBarcode, setFromBarcode] = useState()


  //condition move all
  const [moveAll, setMoveAll] = useState()

  useEffect(() => {
    if (isSuccess.status === 500) {
      setIsSaved(false)
      setConfirmation({ show: false, name: confirmation.name })
    }
    else if (isSuccess.status === 200) {
      setConfirmation({ show: false, name: confirmation.name })
      setIsSaved(true)
    }

  }, [isSuccess])

  useEffect(() => {
    if (!isSaved) setIsSuccess({ success: false, name: null })
  }, [isSaved])
  const postInfo = isSuccess.success && isSuccess.status ? true : false

  // end of submit

  //singleview
  const updateList = (props, props2, moduleName) => {
    setLocalEntryData()
    setSummary()
    if (search === 'barcode') return

    dispatch({ type: IS_LOADING, isLoading: true })
    setTimeout(() => {
      getStockTakeSummary({
        category,
        moduleName,
        stockMovementTargetRepack: props2,
        zeroQuantity: props,
        singleView: true,
        dispatch,
        desc: search,
        warehouseUuid,
        areaId,
        setModule: setSummary,
        module: localEntryData
      },
        setSearch(search))
    }, 10);
  }

  return { fromBarcode, setFromBarcode, updateList, action, setAction, confirmation, setConfirmation, isSuccess, setIsSuccess, isSaved, setIsSaved, createPo, setCreatePo, singleViewData, setSingleViewData, postInfo, moveAll, setMoveAll }
}

