export const useTargetRepack = ({ moduleEntry, activeSku, dispatch, moduleName }) => {
  const targetRepacks = useSelector(state => state.targetRepacks)
  const slotList = useSelector(state => state.slotList)

  const [slot, setSlot] = useState()

  const { gradeList } = moduleEntry.jobPost.list[activeSku]
  const tgtRpck = moduleEntry.jobPost.list[activeSku].poList[0].targetRepacks
  const table = filterTargetRepack(tgtRpck, gradeList, activeSku, moduleEntry, slot)

  const [showTargetRepack, setShowTargetRepack] = useState(true)


  //set Target Repack

  useEffect(_ => {
    setTargetRepack({ moduleEntry, dispatch })
  }, [])

  useEffect(_ => {
    if (!table?.data?.length && !slot?.length) setShowTargetRepack(false)
    if (table?.data?.length) setShowTargetRepack(true)
    updateTargetRepack({ data: table?.data, targetRepacks, activeSku, dispatch })
  }, [table])

  return { showTargetRepack, table, slot, setSlot, slotList }
}

//use target movement
export const useTargetMovement = ({ moduleEntry, activeSku, dispatch, moduleName }) => {
  const targetRepacks = moduleEntry.jobPost.list[activeSku].poList[0].targetMovements
  const slotList = useSelector(state => state.slotList)

  const [slot, setSlot] = useState()

  const { gradeList } = moduleEntry.jobPost.list[activeSku]
  const table = filterTargetRepack(targetRepacks, gradeList, activeSku, moduleEntry, slot, true)

  const [showTargetRepack, setShowTargetRepack] = useState(true)

  useEffect(_ => {
    if (!table?.data?.length && !slot?.length) setShowTargetRepack(false)
    if (table?.data?.length) setShowTargetRepack(true)
    updateTargetRepack({ data: table?.data, targetRepacks, activeSku, dispatch })
  }, [table])

  return { showTargetRepack, table, slot, setSlot, slotList }
}

//use filter new item to active
export const useIsNewItem = ({ setActiveSKU, moduleEntry, selectedSku, dispatch, moduleName }) => {
  const sorted = null
  useEffect(() => {
    let skuName = null
    const newSkuList = moduleEntry?.jobPost?.list?.filter(data => data.isNewItem)
    const length = newSkuList?.length
    if (length) skuName = newSkuList[length - 1]?.skuName
    const idx = moduleEntry?.jobPost?.list?.findIndex(data => data.skuName === skuName)
    if (idx >= 0) setActiveSKU(idx)

  }, [selectedSku]);
  return { sorted }
}
